body {
 display: flex;
 flex-direction: column;
 margin: 0;
 height: 100%;
}

#body {
 margin: 0 auto;
 padding-top : 83px;
 width: 90%;
 max-width: 1200px;
 height: 100%;
 min-height: calc(100vh - 85px);
 flex-grow: 1;
}

#nav-bar {
 position: fixed;
 top: 0;
 left: 0;
 z-index: 1000;
 width: 100%;
 height: 80px;


 #nav-links {
 float: right;
 margin: 20px;
 height: 40px;

 display: flex;
 justify-content: space-between;
 align-items: center;
 gap: 30px;
 }

 #menu-bg {
 display: none;
 }

 .progress {
 position: fixed;
 top: 80px;
 left: 0;
 z-index: 1000;
 height: 3px;
 }
}

#foot {
 position: relative;
 bottom: 0;
 left: 0;
 width: 100%;

 display: flex;
 justify-content: center;
 align-items: center;
 flex-direction: column;

 #foot-bar {
 width: 100%;
 height: 8px;
 }

 #foot-content {
 display: flex;
 justify-content: left;
 align-items: start;
 flex-wrap: wrap;
 flex-direction: row;
 gap: 20px;
 flex: 1;
 width: 100%;

 #foot-main {
 width: 600px;
 }

 #foot-socials {
 width: 300px;
 }
 }
}

#theme-selector {
 position: fixed;
 bottom: 20px;
 right: 20px;
 width: 50px;
 height: 50px;
 padding: 10px;
}

p, h1, h2, h3, h4, h5, h6, ol, ul, .p-margins {
 margin: 20px 10%;
}

li p {
 margin: 10px 0;
}

hr {
 margin: 50px 80px;
 width: calc(100% - 160px);
}

figure {
 margin: 0;
}