@media (max-width: 1000px) {
 #nav-bar {
 #nav-links {
 display: block;
 position: fixed;
 top: -711px;
 left: 0;
 z-index: -1;
 width: 100%;
 height: auto;
 margin: 0;
 float: none;

 a {
 display: block;
 padding: 10px;
 margin-left: 30px;
 width: calc(100% - 30px);
 height: 50px;
 line-height: 50px;
 }
 }

 #menu-bg {
 display: block;
 position: fixed;
 top: 0;
 left: 0;
 z-index: 1000;
 width: 100%;
 height: 80px;

 #menu {
 float: right;
 margin: 20px;
 width: 40px;
 height: 40px;
 z-index: 1000;
 }
 }
 }
}

@media (max-width: 850px) {
 #body {
 width: 100%;
 }
}

@media (max-width: 950px) {
 #foot {
 #foot-content {

 flex-direction: column;
 justify-content: center;

 #foot-main {
 margin: auto;
 text-align: center;
 }

 #foot-socials {
 margin: auto;
 }
 }
 }
}

@media (max-width: 600px) {
 #foot {
 #foot-content {
 #foot-main {
 width: 100%;
 }
 }
 }
}