.carousel {
 position: relative;
 overflow: hidden;
 
 .track {
 display: flex;
 flex-direction: row;
 gap: 100px;
 
 .card {
 margin: 20px;
 min-width: 350px;
 }
 }

 .prev-button, .next-button {
 position: absolute;
 top: 50%;
 width: 50px;
 height: 50px;
 transform: translateY(-50%);
 font-size: xx-large;
 }

 .prev-button {
 left: 10px;
 }
 .next-button {
 right: 10px;
 }
}